/* Ensure all columns have the same height */
.row-eq-height {
  display: flex;
  flex-wrap: wrap;
}

/* Ensure the AdminMenu and product cards are displayed side by side */
.row {
  display: flex;
  flex-wrap: nowrap;
}

/* Style for the AdminMenu sidebar */
.pod-sidebar {
  width: 25%;
  padding-right: 20px;
}

/* Adjust the column width for medium and small screens */
/* Adjust the column width for medium screens to distribute products evenly */
@media (max-width: 992px) {
  .col-md-4 {
    flex: 0 0 33.33%; /* Display three products in a row */
    max-width: 33.33%;
  }
  .col-md-4:nth-child(2n) {
    margin-left: 0; /* Remove margin for even columns */
  }
}

/* Adjust the column width for larger screens to distribute products evenly */
@media (min-width: 1200px) {
  .col-md-4 {
    flex: 0 0 33.33%; /* Display three products in a row */
    max-width: 33.33%;
  }
  .col-md-4:nth-child(2n) {
    margin-left: 0; /* Remove margin for even columns */
  }
}

@media (max-width: 768px) {
  .col-md-4 {
    flex: 0 0 50%; /* Display two products in a row */
    max-width: 50%;
  }
}

.pod-card {
  border: 1px solid #ddd; /* Card border */
  border-radius: 10px; /* Rounded corners */
  transition: transform 0.2s;
  height: 100%; /* Make all cards the same height */
  display: flex;
  flex-direction: column; /* Ensure the card content is properly aligned */
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1); /* Add a subtle box shadow */
  margin-bottom: 20px; /* Add margin at the bottom of each card */
  margin-left: 40px;
  width: 300px;
}

.pod-card:hover {
  transform: scale(1.02); /* Scale up the card on hover */
}

/* Set a fixed height for the card body to make all cards the same size */
.pod-card-body {
  flex-grow: 1;
  padding: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

/* Ensure the card title and text don't overflow */
.pod-card-title,
.pod-card-text {
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Footer styles */
.pod-card-footer {
  background-color: #f4f4f4;
  text-align: center;
}

/* Style for the card image */
.pod-img {
  height: 200px; /* Set a fixed height for the images */
  width: 100%; /* Make all images the same width */
  object-fit: cover; /* Maintain aspect ratio */
}

/* Style for the product name and description */
.pod-card-title {
  margin-bottom: 10px;
  font-size: 18px;
  font-weight: bold;
}

.pod-card-text {
  font-size: 14px;
}
