/* CSS Styles for Girly Dashboard Component */
.dashboard-container {
  background-color: #fff1f4; /* Light pink background */
  padding: 20px;
}

.dashboard-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
  color: #ff69b4; /* Pink text color */
  text-align: center;
}

.dashboard-info {
  font-size: 16px;
  color: #333;
}

.dashboard-info-row {
  margin-bottom: 10px;
}

.dashboard-info-label {
  font-weight: bold;
  color: #ff69b4; /* Pink text color */
}

.dashboard-info-value {
  margin-left: 10px;
}

.dashboard-info-text {
  color: #777; /* Light gray text color */
}

/* Add responsive styles for smaller screens if needed */
@media (max-width: 768px) {
  .dashboard-card {
    padding: 10px;
  }

  .dashboard-title {
    font-size: 20px;
  }

  .dashboard-info {
    font-size: 14px;
  }
}
