/* Category Form */
.category-form {
  text-align: center;
}

/* Category Input Container */
.category-input-container {
  margin-bottom: 20px;
}

/* Category Input */
.category-input {
  width: 80%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  transition: border-color 0.2s ease-in-out;
}

/* Category Button */
.category-button {
  background-color: #ff4da3;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 20px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
}

/* Category Button Hover Style */
.category-button:hover {
  background-color: #ff3685;
}

/* Category Button Focus Style */
.category-button:focus {
  outline: none;
}
