/* CSS Styles for SearchInput Component */
.search-container {
  width: 100%;
  text-align: center;

  background-color: #ff69b4;
  border-radius: 25px;
  color: white;
  margin-bottom: 20px;
}

.search-input-container {
  position: relative;
  width: 100%;
}

.search-input {
  width: 100%;
  padding: 10px;
  border: none;
  border-radius: 50px;
  font-size: 16px;
  text-align: center;
  background-color: #ffbbdd;
  color: #000;
}
.search-input::placeholder {
  color: white;
}
.search-button {
  position: absolute;
  right: 0;
  top: 0;
  width: 60px; /* Adjust the width as needed */
  height: 100%;
  background-color: #1f7cdf;
  border: none;
  border-radius: 20px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

/* Add styles for smaller screens */
@media (max-width: 768px) {
  .search-input-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .search-button {
    position: relative;
    top: 10px;
  }
  .search-container {
    width: 50%;
    text-align: center;
  }
}
