.about-container {
  color: #212121;
  font-family: Lora, serif;
  font-style: italic;
  font-weight: 500;
  font-size: 17pt;
  line-height: 1.2;
  width: 80%;
  margin: 20px auto;
  margin-top: 40px;
}

.about-heading {
  color: #7f1146;
  font-family: lora, serif;
  font-size: 19pt;
  letter-spacing: normal;
  font-weight: 500;
  line-height: 1.2;
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.image-container {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

.about-description {
  margin: 20px auto;
  margin-top: 40px;
}
.extra-section {
  display: flex;
  align-items: flex-start; /* Align items to the top */
  margin-top: 40px;
}

.extra-image {
  width: 40%;
  height: auto;
  margin-right: 20px;
}

.extra-content {
  width: 60%;
}

.extra-heading {
  font-size: 30px;
  font-weight: 600;
  margin-bottom: 10px;
  text-align: center;
  color: #7f1146;
}

.extra-paragraph {
  font-size: 18px;
  line-height: 1.4;
}
