/* HeaderStyle.css */

/* Navbar styles */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 60px;
  background-color: transparent;
  transition: background-color 0.3s ease;
  z-index: 1;
}

.navbar.scrolled {
  background-color: black;
}

/* Navbar container styles */
.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 0 20px;
}

/* Navbar logo styles */
.navbar-logo {
  color: white;
  font-size: 20px;
  text-decoration: none;
}

/* Navigation links styles */
.nav-links {
  list-style: none;
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
}

.nav-item {
  margin-left: 20px;
}

.nav-link {
  color: white;
  text-decoration: none;
  transition: color 0.3s ease;
}

.nav-link:hover {
  color: pink;
}

/* Active link styles */
.navbar.scrolled .nav-link.active {
  color: #ff4da3;
  /* Add any additional styles for the active link when scrolled */
}

/* Mobile menu styles */
.mobile-menu {
  display: none;
  cursor: pointer;
}

/* Mobile menu bar styles */
.bar {
  width: 25px;
  height: 3px;
  background-color: white;
  margin-bottom: 5px;
  transition: transform 0.3s, opacity 0.3s;
}

.mobile-menu.open .bar:nth-child(1) {
  transform: translateY(8px) rotate(45deg);
}

.mobile-menu.open .bar:nth-child(2) {
  opacity: 0;
}

.mobile-menu.open .bar:nth-child(3) {
  transform: translateY(-8px) rotate(-45deg);
}

/* Responsive styles for smaller screens */
@media (max-width: 768px) {
  .nav-links {
    position: fixed;
    top: 80px;
    left: 0;
    width: 100%;
    height: calc(100vh - 80px);
    background-color: black;
    opacity: 0;
    visibility: hidden;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: opacity 0.3s, visibility 0.3s;
    z-index: 998;
  }
  .navbar-container {
    padding: 0; /* Remove padding to span the whole width */
    max-width: 100%; /* Ensure the container spans the full width */
  }
  .nav-links.mobile-menu-open {
    opacity: 1;
    visibility: visible;
  }

  .nav-item {
    margin: 10px 0;
  }

  .mobile-menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
/* Dropdown menu styles */
.dropdown-menu {
  background-color: #fff; /* Dropdown background color */
  border: 1px solid #ccc; /* Dropdown border */
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1); /* Dropdown box shadow */
  padding: 10px 0; /* Dropdown content padding */
  max-height: 200px; /* Max height for scrollable dropdown */
  overflow-y: auto; /* Enable vertical scroll if needed */
}

.dropdown-item {
  color: #333; /* Dropdown item text color */
  padding: 5px 20px; /* Dropdown item padding */
  transition: background-color 0.3s ease; /* Smooth background color transition */
}

.dropdown-item:hover {
  background-color: #f5f5f5; /* Background color on hover */
  color: #000; /* Text color on hover */
}
