/* Product Details Container */
.product-details-container {
  padding: 20px;
}

/* Product Details Row */
.product-details-row {
  display: flex;
  flex-wrap: wrap;
}

/* Product Details Column - Carousel */
.product-details-col.product-details-carousel {
  flex: 1;
  max-width: 50%;
  margin-right: 20px;
}

/* Product Carousel Image */
.product-carousel-image {
  width: 100%;
  max-height: 400px;
  object-fit: cover;
}

/* Product Details Column - Info */
.product-details-col.product-details-info {
  flex: 1;
}

/* Product Details Title */
.product-details-title {
  font-size: 24px;
  font-weight: bold;
  margin-top: 0;
  color: #7f1146;
}

/* Product Details Subtitle */
.product-details-subtitle {
  font-size: 18px;
  font-weight: bold;
  margin-top: 20px;
  align-items: center;
  color: #7f1146;
}

/* Product Details Text */
.product-details-text {
  font-size: 16px;
  margin-top: 2px;
}

/* Product Details Buttons */
.product-details-buttons {
  margin-top: 20px;
}

/* Product Details Button */
.product-details-button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #0056b3;
  color: white;
  border: none;
  border-radius: 20px;
  cursor: pointer;
}

.product-details-button:disabled {
  background-color: #999;
  cursor: not-allowed;
}

/* Product Details Similar Section */
.product-details-similar {
  margin-top: 30px;
}

/* No Similar Products Found Message */
.product-details-no-similar {
  font-size: 16px;
  text-align: center;
}

/* Product Card Image */
.product-card-image {
  width: 100%;
  max-height: 180px;
  object-fit: cover;
}

/* Product Card Body */
.product-card-body {
  padding: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

/* Product Card Title */
.product-card-title {
  font-size: 18px;
  font-weight: bold;
  margin-top: 0;
}

/* Product Card Text */
.product-card-text {
  font-size: 14px;
  margin-top: 5px;
}

/* Product Card Footer */
.product-card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
}

/* Product Card Price */
.product-card-price {
  font-size: 16px;
  font-weight: bold;
}

/* Product Card Buttons */
.product-card-buttons {
  margin-top: 10px;
}

/* Product Card Button - Details */
.product-card-button.product-card-details-button {
  padding: 5px 10px;
  font-size: 14px;
  background-color: #1665da;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

/* Product Card Button - Add To List */
.product-card-button.product-card-add-button {
  padding: 5px 10px;
  font-size: 14px;
  background-color: #09143c;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.product-card-button:disabled {
  cursor: not-allowed;
  background-color: #999;
}

/* Responsive Styling */
@media (max-width: 768px) {
  /* Adjust styles for smaller screens */
  .product-details-row {
    flex-direction: column;
  }

  /* Reduce margin for buttons on smaller screens */
  .product-details-buttons {
    margin-top: 10px;
  }

  /* Center align buttons on smaller screens */
  .product-details-button,
  .product-card-button {
    display: block;
    margin: 10px auto;
  }

  /* Reduce font size for similar products message on smaller screens */
  .product-details-no-similar {
    font-size: 14px;
  }
}
