.contact-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: #212121;
    font-family: Lora, serif;
    font-style: italic;
    font-weight: 500;
    font-size: 17pt;
    line-height: 1.5;
    width: 80%;
    margin: 20px auto;
    margin-top: 40px;
  }
  
  .about-heading {
    color: #7f1146;
    font-family: Lora, serif;
    font-size: 19pt;
    letter-spacing: normal;
    font-weight: 500;
    line-height: 1.2;
    margin-top: 30px;
  }
  
  .contact-info {
    margin-top: 20px;
  }
  
  .contact-day {
    margin-top: 10px;
  }
  
  .contact-location {
    margin-top: 20px;
  }
  
  .other-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 30px;
  }
  
  .other-image {
    max-width: 100%;
  }
  
  .other-content {
    width: 100%;
    max-width: 600px;
    margin: 20px;
    padding: 10px;
  }
  
  .other-content iframe {
    width: 100%;
    height: 450px;
    border: 0;
    margin: 0 auto;
    display: block;
    padding: 10px;
  }
  
  /* Mobile Styles */
  @media (max-width: 767px) {
    .contact-container {
      font-size: 14pt;
    }
  
    .about-heading {
      font-size: 16pt;
      margin-top: 20px;
    }
  
    .contact-info,
    .contact-day,
    .contact-location {
      margin-top: 15px;
      font-size: 12pt;
    }
  
    .extra-content iframe {
      height: 250px;
      max-height: 80vh;
    }
  }
  