body {
  position: relative;
  min-height: 100vh;
}

main {
  padding-bottom: 84px; /* height of the footer */
  min-height: calc(100vh - (60px + 84px)); /* height of header + footer */
}

footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 70px;
  background-color: #f5f5f5;
}
