/* Cat Container */
.cat-container {
  padding: 20px;
  background-color: #f4f4f4;
  min-height: 100vh; /* Fill the available vertical space */
}

/* Cat Row */
.cat-row {
  display: flex;
  min-height: 100%; /* Fill the available vertical space */
}

/* Cat Sidebar (Admin Menu) */
.cat-sidebar {
  width: 25%;
  padding-right: 20px;
}

/* Cat Main Content */
.cat-main {
  flex-grow: 1;
  display: flex;
  justify-content: center; /* Center the content horizontally */
  align-items: center; /* Center vertically */
}

/* Cat Content */
.cat-content {
  width: -webkit-fill-available;
  text-align: center; /* Center the text within the content */
}

/* Cat Title */
.cat-title {
  align-items: center;
  font-size: 24px;
  margin-bottom: 20px;
  color: #333;
}

/* Cat Form */
.cat-form {
  width: 50%; /* Adjust the width as needed */
  margin: 0 auto; /* Center horizontally */
  margin-bottom: 10;
}

/* Cat List */
.cat-list {
  width: 100%;
  margin-bottom: 10;
}

/* Cat Table */
.cat-table {
  width: 100%;
}

/* Cat Edit Button */
.cat-edit-btn {
  background-color: #007bff;
  color: #fff;
  padding: 5px 15px;
  border: none;
  border-radius: 20px;
  margin-right: 50px;
  cursor: pointer;
}

/* Cat Delete Button */
.cat-delete-btn {
  background-color: #dc3545;
  color: #fff;
  padding: 5px 15px;
  border: none;
  border-radius: 20px;
  cursor: pointer;
}
/* Category Container */
.cat-container {
  padding: 20px;
  background-color: #f4f4f4;
  min-height: 100vh;
}

/* Category Row */
.cat-row {
  display: flex;
  min-height: 100%;
}

/* Category Sidebar (Admin Menu) */
.cat-sidebar {
  width: 25%;
  padding-right: 20px;
}

/* Category Main Content */
.cat-main {
  flex-grow: 1;
  display: flex;
  justify-content: center; /* Center the content horizontally */
  align-items: flex-start;
}

/* Category Form */
.cat-form {
  width: 80%;
}

/* Category Form Group */
.cat-form-group {
  margin-bottom: 20px;
}

/* Category Label */
.cat-label {
  font-weight: bold;
  color: #555;
}

/* Category Input */
.cat-input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  transition: border-color 0.2s ease-in-out;
}

/* Category Textarea */
.cat-textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  transition: border-color 0.2s ease-in-out;
}

/* Category Select */
.cat-select {
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  transition: border-color 0.2s ease-in-out;
}

/* Category Photo Row */
.cat-photo-row {
  display: flex;
}

/* Category Photo Column */
.cat-photo-col {
  width: 33.33%;
  padding: 10px;
}

/* Category Photo Label */
.cat-photo-label {
  display: block;
  border: 1px dashed #ddd;
  border-radius: 5px;
  padding: 10px;
  text-align: center;
  cursor: pointer;
}

/* Category Photo Preview */
.cat-photo-preview {
  text-align: center;
  margin-top: 10px;
}

/* Category Photo */
.cat-photo {
  max-width: 100%;
  height: auto;
}

/* Category Button Container */
.cat-button-container {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

/* Category Button */
.cat-button {
  background-color: #007bff;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 20px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
}

/* Category Button Hover Style */
.cat-button:hover {
  background-color: #0056b3;
}

/* Category Button Focus Style */
.cat-button:focus {
  outline: none;
}

