/* Center the page content vertically and horizontally */
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #ffd9e6; /* Light pink background */
}

/* Image container styles */
.image-container {
  max-width: 400px; /* Adjust the image size as needed */
  height: auto;
  margin-right: 50px; /* Spacing between image and form */
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  animation: slideInLeft 1s ease-in-out;
}

/* Form container styles */
.form-container {
  background-color: #ffd9e6; /* Light pink background */
  padding: 20px;
  height: auto; /* Allow the form to expand as needed */
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  text-align: center;
  width: 400px; /* Adjust the form width as needed */
  animation: slideInRight 1s ease-in-out;
  margin-bottom: 300px; /* Add margin to push the form up */
}

/* Title styles */
.title {
  text-align: center;
  font-size: 24px;
  margin-bottom: 20px;
  color: #ff4da3; /* Pinkish title color */
}

/* Input styles */
.form-control {
  border: none;
  border-radius: 5px;
  margin-bottom: 20px; /* Spacing between inputs */
  padding: 10px 20px;
  background-color: #f5f5f5; /* Light gray input background color */
  font-size: 16px;
  width: 80%;
  transition: background-color 0.3s ease-in-out;
}

.form-control:focus {
  background-color: #fff; /* White input background on focus */
  outline: none;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

/* Button styles */
.btn-prime {
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  background-color: #ff4da3; /* Pink button background color */
  color: #fff; /* White button text color */
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.btn-prime:hover {
  background-color: #ff3685; /* Darker pink on hover */
}

/* Forgot Password link styles */
a {
  text-decoration: none;
  color: #333; /* Link text color */
  font-size: 14px;
  margin-top: 10px; /* Spacing between button and link */
  display: block; /* Display as block for full width */
}

a:hover {
  color: #ff4da3; /* Pink link color on hover */
}

/* Keyframe animations */
@keyframes slideInLeft {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}

@keyframes slideInRight {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
  }
}
@media (max-width: 768px) {
  /* Override styles for mobile screens */

  /* Center the page content vertically and horizontally */
  .login-container {
    padding: 20px; /* Adjust padding for mobile screens */
    flex-direction: column; /* Stack image and form vertically on small screens */
  }

  /* Adjust image container styles for mobile */
  .image-container {
    max-width: 100%; /* Ensure the image is responsive */
    margin: 0 auto; /* Center the image horizontally */
  }

  /* Adjust form container styles for mobile */
  .form-container {
    max-width: 100%; /* Ensure the form is full width on mobile */
    width: 100%; /* Ensure the form is full width on mobile */
    background-color: #ffd9e6; /* Light pink background */
  }

  /* Add specific mobile styles if needed */
}
