/* Homepage Styles */
.homepage-title {
  color: #7f1146;
  font-family: Lora, serif;
  font-size: 34pt;
  font-weight: 400;
  line-height: 1.2;
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.homepage-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px auto;
  flex-wrap: wrap;
  width: 80%;
}

.homepage-item {
  width: 23%;
  text-align: center;
}

.homepage-image-container {
  width: 100%;
  max-width: 250px;
  height: 250px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.homepage-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.homepage-item-title {
  color: #7f1146;
  font-family: Lora, serif;
  font-size: 19pt;
  font-weight: 400;
  line-height: 1.4;
  margin-top: 10px;
}

.homepage-section-title {
  color: #7f1146;
  font-family: Lora, serif;
  font-size: 25px;
  font-weight: 700;
  line-height: 1.2;
  margin-top: 55px;
  text-align: center;
  width: 100%;
  margin-bottom: 20px;
}

.homepage-section-description {
  color: #7f1146;
  font-family: Lora, serif;
  font-size: 18px;
  line-height: 1.5;
  width: 80%;
  margin: 0 auto 20px;
  display: flex;
}

@media (max-width: 768px) {
  .homepage-title {
    font-size: 24px;
    margin-top: 20px;
  }

  .homepage-container {
    width: 80%;
    padding: 0 20px;
    margin: 0;
  }

  .homepage-item {
    width: 50%;
    margin-bottom: 20px;
  }

  .homepage-item-title {
    font-size: 16pt;
  }

  .homepage-section-title {
    font-size: 20px;
    margin-top: 30px;
    margin-bottom: 10px;
  }

  .homepage-section-description {
    font-size: 16px;
    margin-bottom: 15px;
  }
}
