@keyframes slideInFromLeft {
  from {
    transform: translateX(-100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/* Center the page content vertically and horizontally */
.registration-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #ffd9e6; /* Background color for the whole page */
  position: relative; /* Add this line */
}

/* Form container styles */
.registration-form {
  background-color: #fff; /* Form background color */
  padding: 20px;
  height: auto;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  text-align: center;
  background-color: #ffd9e6; /* Background color for the whole page */
  width: 400px; /* Adjust the form width as needed */
  animation: slideInFromLeft 1s ease-in-out;
  margin-bottom: 300px; /* Add margin to push the form up */
}

/* Form title styles */
.registration-title {
  font-size: 24px;
  margin-bottom: 20px;
  color: #ff4da3; /* Title color */
}

/* Input field styles */
.registration-input-wrapper {
  margin-bottom: 20px;
}

.registration-input {
  border: 2px solid #ddd; /* Pink input border color */
  border-radius: 5px;
  padding: 10px;
  width: 80%;
  font-size: 16px;
}

/* Button styles */
.registration-button {
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  background-color: #ff4da3; /* Button background color */
  color: #fff; /* Button text color */
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  animation: fadeIn 1s ease-in-out;
}

.registration-button:hover {
  background-color: #ff3685; /* Button background color on hover */
}

/* Image styles */
.registration-image {
  max-width: 400px; /* Adjust the image size as needed */
  height: auto;
  margin-right: 50px; /* Spacing between image and form */
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}
/* Add these keyframes for slide-in and fade-in animations */
@keyframes slideInFromLeft {
  from {
    transform: translateX(-100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Apply the animations to the form and button */
.registration-form {
  animation: slideInFromLeft 1s ease-in-out;
  /* Other styles... */
}

.registration-button {
  animation: fadeIn 1s ease-in-out;
  /* Other styles... */
}
@media (max-width: 768px) {
  /* Override styles for mobile screens */

  /* Center the page content vertically and horizontally */
  .registration-container {
    padding: 20px; /* Adjust padding for mobile screens */
    flex-direction: column; /* Stack image and form vertically on small screens */
    margin-bottom: 200;
  }

  /* Adjust image container styles for mobile */
  .registration-image {
    max-width: 100%; /* Ensure the image is responsive */
    margin: 0 auto; /* Center the image horizontally */
  }
  .registration-button {
    animation: fadeIn 1s ease-in-out;
    /* Other styles... */
  }
  /* Adjust form container styles for mobile */
  .registration-form {
    max-width: 100%; /* Ensure the form is full width on mobile */
    width: 100%; /* Ensure the form is full width on mobile */
    background-color: #ffd9e6; /* Background color for the whole page */
  }

  /* Add specific mobile styles if needed */
}
