/* Center everything in the middle */
.dashboard-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #ffebec;
}

/* Improved Card style */
.dashboard-card {
  background-color: #ffebec;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 30px;
  text-align: center;
  max-width: 400px; /* Limit card width */
}

/* Heading style */
.dashboard-heading {
  font-size: 24px;
  margin-bottom: 20px;
  color: #333;
}

/* Form label style */
.dashboard-label {
  font-weight: bold;
  color: #555;
}

/* Form input style */
.dashboard-input {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  width: 80%;
  margin-bottom: 15px;
  font-size: 16px;
  transition: border-color 0.2s ease-in-out;
  color: #333;
}

/* Form input hover/focus style */
.dashboard-input:hover,
.dashboard-input:focus {
  border-color: pink;
  outline: none;
}

